* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
html {
  min-width: 320px;
}
.container {
  width: 100%;
  height: 100%;
  max-width: 1170px;
  min-width: 320px;
  z-index: 2;
}
header {
  position: absolute;
  z-index: 5;
  width: 100%;
  height: 83px;
  display: flex;
  justify-content: center;
}
header .container {
  display: flex;
  justify-content: flex-end;
}
header .container .logo {
  display: flex;
  align-items: center;
  margin-right: 87px;
}
header .container .logo #great-img {
  animation: rotate;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
header .container .logo #image {
  animation: rotate-2;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  margin-right: 17px;
}
header .container .logo p {
  font-size: 22px;
  font-weight: 500;
  color: #383334;
}
header .info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 650px;
}
header .info .item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
header .info .item img {
  margin-right: 12px;
}
header .info .item p {
  text-align: left;
}
main {
  min-width: 320px;
  display: flex;
  flex-direction: column;
}
main h1 {
  font-size: 44px;
}
main h2 {
  font-size: 47px;
}
main .first {
  width: 100%;
  height: 720px;
  position: relative;
  display: flex;
  justify-content: center;
}
main .first .container {
