  align-items: center;
  justify-content: flex-end;
  z-index: 2;
  position: absolute;
}
main .first .container .transparent-block {
  display: none;
  position: absolute;
  border-bottom-right-radius: 43%;
  width: 100%;
  height: 620px;
  background-color: #f1f6f7;
  z-index: 2;
  opacity: 0.8;
}
main .first .container .img-container {
  position: absolute;
  left: -220px;
  width: 760px;
  height: 835px;
  margin-right: 14px;
  z-index: -4;
}
main .first .container .img-container .legs {
  z-index: 1;
  position: absolute;
  width: 700px;
}
main .first .container .img-container .circle-are {
