}
main .first .container .info-block .points .point .circle {
  border-radius: 50%;
  width: 60px;
  height: 60px;
  padding: 12px 20px;
  margin-right: 20px;
  background-color: #a7c93d;
  border: 7px solid #e8edc9;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
main .first .container .order-form {
  margin-top: 40px;
  margin-right: 20px;
  width: 270px;
  height: 555px;
  background-color: white;
  border: 7px solid #a8ca3f;
  border-radius: 15px;
